  background: -webkit-linear-gradient(46.98deg, #E900FF 0%, #1E00FF 100%);
  background: linear-gradient(43.02deg, #E900FF 0%, #1E00FF 100%); }
  .Halo.active {
    -webkit-transform: scale(1);
            transform: scale(1); }

